.portfolio-slider {
    width: 100%;
    padding: 60px 0;
    overflow: hidden;  /* 추가 */
  }

  .slide {
    transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1); /* 부드러운 이징 함수 사용 */
  }

  .slick-track {
    display: flex;
    align-items: center;
  }
  
  .slide-content {
    position: relative;
    width: 80%; /* 비율 유지를 위해 퍼센트 사용 */
    padding-top: 51.2%; /* 16:9 비율 유지 (601 / 940 = 0.512) */
    overflow: hidden;
    border-radius: 56px;
    transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1); /* 부드러운 이징 함수 사용 */
    margin: 0 auto;
  }
  
  .slide-content img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .slide-text {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 20px;
    color: white;
  }

  
  .slick-center .slide-content {
    width: 100%; /* 센터 슬라이드는 전체 너비 사용 */
    padding-top: 63.9%; /* 16:9 비율 유지 (748 / 1170 = 0.639) */
  }
  
  .slick-slide:not(.slick-center) .slide-content::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5); /* 비활성화된 슬라이드를 어둡게 만듦 */
    transition: all 300ms ease;
  }
  
  .slick-slide:not(.slick-center) {
    cursor: pointer;
  }
  
  .slick-prev, .slick-next {
    z-index: 1;
  }
  
  .slick-prev {
    left: 15px;
  }
  
  .slick-next {
    right: 15px;
  }

  /* 슬라이드 전환 애니메이션 개선 */
.slick-slide {
    transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1), opacity 500ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  /* 슬라이더 전체 크기 조정 */
  .slick-list {
    overflow: visible;
  }

  .slide-text {
    font-family: 'pretendard-bold';
    letter-spacing: -1.5px;
    padding: 15px;
    transform: scale(0.8);  /* 기본 크기를 약간 작게 설정 */
    color: white;
  }

  .slide-text h3 {
    font-size: 0px;
    margin-bottom: 8px;
  }

  .description {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* 왼쪽 정렬 */
    margin: 0;
    color: white;
  }

  .description span {
    font-size: 0px;
    font-family: 'pretendard-bold';
    letter-spacing: -2.4px;
    line-height: 1.5;
    text-align: left;
  }

/* 센터 슬라이드의 텍스트 스타일 */
.slick-center .slide-text {
    padding: 50px;
    transform: scale(1);  /* 원래 크기로 복원 */
  }
  
  .slick-center .slide-text h3 {
    font-size: 48px;  /* 더 큰 크기로 설정 */
  }
  
  .slick-center .description span {
    font-size: 34px;  /* 더 큰 크기로 설정 */
  }
  
  @media (max-width: 1200px) {
    .slide {
      padding: 0 20px;
    }
  }
  
  @media (max-width: 768px) {
    .slide {
      padding: 0 10px;
    }
    .slide-content {
      border-radius: 28px;
    }
  }