.navbar-container {
    position: fixed;
    top: 50px;
    right: 50px;
    background-color: #FFD100;
    border-radius: 41px;
    width: 484px;
    height: 81px;
    display: flex;
    align-items: center;
    justify-content: space-between; /* 공간을 균등하게 분배 */
    transition: width 0.25s ease, transform 0.25s ease; /* width와 transform 속성에 애니메이션 적용 */
    transform-origin: right center; /* 변환 기준점을 오른쪽으로 설정 */
    overflow: visible; /* overflow 속성을 visible로 변경 */
    z-index: 999;
  }
  
  .navbar-container.open {
    width: 950px; /* 넓이를 증가 */
    transform: translateX(0); /* 좌측으로 커지도록 설정 */
  }
  
  .navbar-container.open.fixed {
    width: 484px; /* 네비게이터의 너비를 고정 */
    height: auto;
    transform: none; /* 변환 효과를 없앰 */
}

  .navbar-logo {
    width: 98.65px;
    height: 27.51px;
    margin-left: 49.68px;
    margin-top: 27px;
    margin-bottom: 27px;
    flex-shrink: 0;
  }
  
  .navbar-logo img {
    width: 100%;
    height: auto; /* 높이를 auto로 설정하여 비율 유지 */
  }
  
  .navbar-menubar {
    width: 192px;
    height: 51px;
    border-radius: 41px;
    background-color: #000;
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-top: 15px;
    margin-right: 31px;
    margin-bottom: 15px;
    margin-left: auto;
  }
  
  .view-menu-text {
    width: 101px;
    height: 19px;
    color: #FFD100;
    font-family: 'Montserrat-black', sans-serif;
    font-size: 15px;
    font-weight: bold;
    margin-left: 22px;
    margin-top: 16px;
    margin-bottom: 16px;
    margin-right: auto;
  }
  
  .plus-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: 11px;
  }
  
  .circle {
    position: relative;
    width: 29px;
    height: 29px;
    margin-right: 11px;
    border: 1px solid #FFD100;
    border-radius: 50%;
  }

  .minus-circle {
    position: relative;
    width: 29px; /* 플러스와 같은 크기 */
    height: 29px;
    border: 1px solid #000; /* 검정색 테두리 */
    border-radius: 50%;
  }
  
  .vertical-line, .horizontal-line, .minus-line {
    position: absolute;
    background-color: #FFD100;
  }
  

  .vertical-line {
    position: absolute;
    width: 1px;
    height: 10px;
    background-color: #FFD100;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .horizontal-line {
    position: absolute;
    width: 10px;
    height: 1px;
    background-color: #FFD100;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .minus-line {
    width: 15px; /* 마이너스 라인의 길이 */
    height: 2px; /* 마이너스 라인의 두께 */
    background-color: #000; /* 검정색 */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .menu {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    padding-right: 31px;
  }

  .navbar-container.open .menu {
    opacity: 1; /* 열릴 때 투명도를 1로 설정하여 나타남 */
    transform: translateX(0); /* 열릴 때 위치를 원래대로 설정하여 자연스럽게 나타남 */
  }

  .menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    gap: 11px;
    margin-right: 80px;
  }
  
  .menu li {
    background-color: #000;
    border-radius: 41px;
    width: 157px;
    height: 51px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0; /* 패딩을 제거하여 동일한 여백 설정 */
  }
  
  .menu a {
    color: #FFD100;
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-weight: bold;
    padding: 0; /* 패딩을 제거하여 동일한 여백 설정 */
    margin-right: 22px; /* 텍스트 좌우 여백 설정 */
  }

  .close-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 31px; /* 우측 패딩과 동일하게 설정 */
    top: 50%;
    transform: translateY(-50%); /* 세로 중앙 정렬 */
    cursor: pointer; /* 포인터 커서 */
  }

.dropdown-menu {
    position: absolute;
    top: 81px;
    left: 230px;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 0 0 41px 41px;
    overflow: hidden;
    height: 0; /* 기본 상태에서는 숨김 */
    opacity: 0;
    transition: height 0.5s ease, opacity 0.5s ease; /* max-height와 opacity 속성에 애니메이션 추가 */
}

/* 열릴 때 스타일 */
.navbar-container.open .dropdown-menu {
    height: 500px; /* 열릴 때 최대 높이로 설정 */
    opacity: 1;
}

.dropdown-menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 11px;
    width: 100%;
    align-items: center;
}

.dropdown-menu li {
    background-color: #000;
    border-radius: 41px;
    width: 187px;
    height: 51px;
    display: flex;
    align-items: center;
}

.dropdown-menu a {
    color: #FFD100;
    text-decoration: none;
    font-family: 'Montserrat-black', sans-serif;
    font-size: 16px;
    font-weight: bold;
    margin-left: 20px;
}

.plus-icon .circle.open .vertical-line {
    display: none; /* 메뉴가 열릴 때 수직 라인을 숨김 */
}

/* 메뉴가 열렸을 때 close-icon 숨김 - 기본 설정 */
.navbar-container.open .close-icon {
    display: none;
}

/* index 페이지에서 메뉴가 열렸을 때 close-icon 보이도록 설정 */
.navbar-container.index.open .close-icon {
    display: flex; /* flex로 설정하여 보이도록 함 */
}