.work-page {
    padding: 20px;
  }
  
  .work-container {
    margin-top: 20px;
  }
  
  .work-container.album {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .work-container.list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .work-item {
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 5px;
  }
  
  .work-item img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  .list-item {
    position: relative;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .list-item .hover-image {
    display: none;
    position: absolute;
    top: 0;
    right: -220px;
    width: 200px;
    height: 150px;
    overflow: hidden;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
  }
  
  .list-item:hover .hover-image {
    display: block;
  }
  
  .list-item .hover-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  button {
    padding: 10px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }