.section5 {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.section5-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #FFD100;
  margin-left: 50px;
  z-index: 1;
}

.main-text {
  font-family: 'montserrat-black', sans-serif;
  font-size: 89px;
  margin-bottom: 20px;
  text-decoration: none;
  color: #FFD100;
  line-height: 1;
  animation: bounce 3s ease-in-out infinite;
}

.sub-text {
  margin-top: 10px;
  font-family: 'montserrat-light', sans-serif;
  font-size: 25px;
}

.copyright {
  position: absolute;
  bottom: 63px;
  width: 100%;
  text-align: center;
  font-family: 'montserrat-light', sans-serif;
  font-size: 20px;
  color: #FFD100;
}

.back-to-top {
  position: absolute;
  bottom: 50px;
  left: 50px;
  width: 204px;
  height: 51px;
  background-color: #FFD100;
  color: #000;
  border: none;
  border-radius: 41px;
  padding: 10px 20px;
  font-size: 16px;
  font-family: 'montserrat-light', sans-serif;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.back-to-top .arrow {
  margin-left: 40px;
  display: inline-block;
  width: 15px;
  height: 15px;
  border-top: 1px solid #000;
  border-right: 1px solid #000;
}

.back-to-top .arrow::before {
  content: '';
  position: absolute;
  top: 16px;
  right: 47px;
  width: 1px;
  height: 20px;
  background-color: #000;
  transform: rotate(225deg);
}

.back-to-top:hover {
  background-color: #000;
  color: #FFD100;
}

.back-to-top:hover .arrow {
  border-color: #FFD100;
}

.back-to-top:hover .arrow::before {
  background-color: #FFD100;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-10px);
    }
    60% {
        transform: translateY(-5px);
    }
}