.loading-page {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
  }
  
  .loading-logo {
    width: 200px;
    height: auto;
  }
  
  .loading-vision {
    font-size: 24px;
    font-weight: bold;
  }
  
  .loading-wrapper {
    opacity: 1;
    transition: opacity 0.5s ease; /* 페이드아웃 애니메이션 추가 */
  }
  
  .loading-wrapper.fade-out {
    opacity: 0;
  }