.section2 {
    position: relative;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
}

.content-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.text-content {
    position: absolute;
    text-align: center;
    pointer-events: none;
    z-index: 20;
    display: flex;
    flex-direction: column;
    align-items: center; /* Center align child elements horizontally */
    justify-content: center; /* Center align child elements vertically */
    width: 100%; /* Ensure it takes up the full width for centering */
    height: 100%; /* Ensure it takes up the full height for centering */
  }
  

.astronaut-image {
    width: 395px;
    height: 550px;
    opacity: 0.7;
    animation: float 6s ease-in-out infinite;
    pointer-events: auto;
}

.title {
    font-family: 'montserrat-black';
    font-size: 4em;
    margin-bottom: 20px;
    letter-spacing: -1px;
    font-weight: bold;
    color: white;
}

.description {
    font-family: 'pretendard-light';
    max-width: 752px;
    font-size: 2em;
    line-height: 1.5;
    color: white;
    margin: 0 auto;
    letter-spacing: -1px;
}

@keyframes float {
    0% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-20px);
    }
    100% {
        transform: translateY(0px);
    }
}

.scroll-down-container {
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    animation: bounce 2s infinite;
}

.scroll-down-link {
    display: flex;
    flex-direction: column;  /* column-reverse에서 column으로 변경 */
    align-items: center;
    text-decoration: none;
    color: white;
    cursor: pointer;
}

.scroll-down-text {
    font-family: 'montserrat-light';
    font-size: 15px;
    transition: color 0.5s ease; /* Smooth transition for text color */
}

.scroll-down-icon {
    width: 40px;
    height: 40px;
    border: 1px solid white;
    border-radius: 50%;
    position: relative;
    margin-top: 10px;
    cursor: pointer;
    overflow: hidden; /* Ensure the pseudo-element stays within the circle */
    transition: background-color 0.5s ease, border-color 0.5s ease; /* Smooth transition for background and border color */
}

.scroll-down-icon:hover {
    border-color: #FFD100; /* Change border to yellow on hover */
}

.scroll-down-link:hover .scroll-down-text {
    color: #FFD100; /* Change text color to yellow on hover */
}

.scroll-down-link:hover .arrow-down::before,
.scroll-down-link:hover .arrow-down::after {
    background-color: #FFD100; /* Change arrow color to yellow on hover */
}

.arrow-down {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30px;  /* 너비를 20px에서 30px로 증가 */
    height: 30px; /* 높이를 20px에서 30px로 증가 */
}

.arrow-down::before,
.arrow-down::after {
    content: '';
    position: absolute;
    top: 70%;  /* bottom: 0 대신 top: 50%를 사용 */
    width: 50%;
    height: 1px;  /* 선의 두께를 2px에서 3px로 증가 */
    background-color: white;
}

.arrow-down::before {
    left: 0;
    transform: translateY(-50%) rotate(45deg);  /* 회전 중심을 조정 */
    transform-origin: right;  /* 회전의 기준점을 오른쪽으로 설정 */
}

.arrow-down::after {
    right: 0;
    transform: translateY(-50%) rotate(-45deg);  /* 회전 중심을 조정 */
    transform-origin: left;  /* 회전의 기준점을 왼쪽으로 설정 */
}

.scroll-down-text {
    font-size: 14px;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-10px);
    }
    60% {
        transform: translateY(-5px);
    }
}

.scroll-down-container {
    animation: bounce 2s infinite;
}