body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: black;
}

body, body * {
  cursor: none !important;
}

.main-container {
  height: 500vh;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
}

.section, .section2, .section3, .section4, .section5 {
  height: 100vh;
  scroll-snap-align: start;
}