.typing-container {
    position: absolute;
    left: 50px;
    top: 50%;
    transform: translateY(-50%);
    white-space: nowrap;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 300px;
    width: 100%;
  }
  
  .typing-text {
    font-family: 'Montserrat-black', sans-serif;
    font-weight: 900; /* This is equivalent to "Black" weight */
    font-size: 70px;
    line-height: 90px; /* This sets the vertical spacing */
    letter-spacing: 1px; /* This sets the horizontal spacing */
    color: #FFD100;
  }
  
  .line1, .line2 {
    margin-bottom: -10px;
    height: 94.7px; /* Match the line-height to ensure consistent spacing */
  }
  
  .line2 {
    min-height: 94.7px; /* Ensure the second line always takes up space */
  }
  
  .line3 {
    font-family: 'pretendard-light', sans-serif; /* or specify a Korean font */
    margin-top: 0.5em;
    font-size: 25px;
    line-height: 1.5;
    letter-spacing: 0.5px;
  }
  
  .cursor {
    display: inline-block;
    width: 2px;
    height: 90px; /* Match the font size */
    animation: blink 0.7s steps(1) infinite;
    margin-left: 2px;
    vertical-align: middle; /* Change from bottom to middle */
    position: relative; /* Add this */
    top: -10px; /* Adjust this value as needed */
  }
  
  @keyframes blink {
    50% {
      opacity: 0;
    }
  }