.section {
    color:white;
    position: relative;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    background-color: transparent;
    overflow: hidden;
  }
  
  .content-wrapper {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .earth-image {
    position: absolute;
    right: 50px;
    top: 50%;
    transform: translateY(-50%);
    max-width: 40%;
    height: auto;
  }
  
  .spaceman-image {
    position: absolute;
    right: 10%;
    top: 15%;
    max-width: 40%;
    height: auto;
    animation: float 5s ease-in-out infinite;
  }
  
  @keyframes float {
    0% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-30px);
    }
    100% {
      transform: translateY(0px);
    }
  }
  
  .scroll-button {
    position: absolute;
    bottom: 50px;
    left: 50px;
    width: 204px;
    height: 51px;
    background-color: #FFD100;
    color: #000;
    border: none;
    border-radius: 41px;
    padding: 10px 20px;
    font-size: 16px;
    font-family: 'Montserrat-light', sans-serif;
    Font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s; /* 마우스 오버 시 부드럽게 색상 전환 */
  }
  
  .scroll-button .arrow {
    margin-left: 15px;
    display: inline-block;
    width: 15px;
    height: 15px;
    border-bottom: 1px solid #000;
    border-right: 1px solid #000;
  }
  
  .scroll-button .arrow::before {
    content: '';
    position: absolute;
    top: 16px;
    left: 159.5px;
    width: 1px;
    height: 20px;
    background-color: #000;
    transform: rotate(-225deg);
  }
  
  .scroll-button:hover {
    background-color: #000;
    color: #FFD100;
  }
  
  .scroll-button:hover .arrow {
    border-color: #FFD100;
  }
  
  .scroll-button:hover .arrow::before {
    background-color: #FFD100;
  }
  
  /* TypingAnimation 컴포넌트에 대한 스타일 */
  .typing-animation {
    position: absolute;
    top: 30%;
    left: 10%;
    color: white;
    font-size: 2em;
  }