.section3 {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    background-color: transparent;
    position: relative;
  }

  .boxes-container {
    display: grid;
    grid-template-columns: repeat(3, minmax(200px, 300px));
    gap: 55px 80px;
    max-width: 1000px;
    width: 90%;
    position: relative;
  }

  .box {
    width: 100%;
    aspect-ratio: 1 / 1.05;
    border-radius: 35px;
    padding: 20px;
    position: relative;
    overflow: hidden;
  }

  .box:hover {
    animation: shake 0.5s ease-in-out infinite;
  }

  .box-odd {
    background-color: #444444;
    color: white;
  }

  .box-even {
    background-color: #FFD100;
    color: black;
  }

  .box1{
    animation: float 3s ease-in-out infinite;
  }

  .box2 {
    animation: bounce 5s ease-in-out infinite;
  }

  .box3 {
    animation: bounce 4s ease-in-out infinite;
  }

  .box4 {
    animation: bounce 3s ease-in-out infinite;
  }

  .box5 {
    animation: float 4s ease-in-out infinite;
  }

  .box6 {
    animation: float 2s ease-in-out infinite;
  }


  .box-title {
    font-family: 'Montserrat-Black', sans-serif;
    font-size: 18px;
    margin-bottom: 20px;
  }

  .box-description {
    font-family: 'pretendard-light';
    font-size: 12px;
    letter-spacing: 0px;
    margin-bottom: 30px;
    Line-height: 1.5;
  }

  .box-tags {
    display: flex;
    margin-top: 5px;
    gap: 10px 5px;
    Justify-content: start;
  }

  .box-tag {
    padding: 10px;
    border-radius: 15px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.2);
  }

  .box-tag-text {
    font-family: 'Montserrat-Black', sans-serif;
    font-size: 10px;
    text-align: center;
    white-space: nowrap;
  }

  .box-image1 {
    position: absolute;
    bottom: 25px;
    right: 25px;
    max-width: 25%;
    max-height: 25%;
  }

  .box-image2 {
    position: absolute;
    bottom: 20px;
    right: 33px;
    max-width: 35%;
    max-height: 35%;
    transform: rotate(20deg);
  }

  .box-image3 {
    position: absolute;
    bottom: 25px;
    right: 25px;
    max-width: 25%;
    max-height: 25%;
    transform: rotate(15deg);
  }

  /* 개별 태그 스타일링 */
  .tag1-1, .tag1-2, .tag1-3, .tag1-4 {
    width: 35px;
    height: 12px;
  }

  .tag1-5, .tag2-2, .tag2-3, .tag2-6, .tag4-1, .tag6-5 {
    width: 70px;
    height: 12px;
  }

  .tag2-4 {
    width: 105px;
    height: 12px;
  }

  .tag2-1, .tag2-5, .tag3-2, .tag4-2, .tag4-3, .tag4-4, .tag4-6, .tag6-1, .tag6-2, .tag6-3, .tag6-4 {
    width: 95px;
    height: 12px;
  }

  .tag3-1, .tag4-5, .tag5-1 {
    width: 120px;
    height: 12px;
  }

  .tag5-3 {
    width: 140px;
    height: 12px;
  }

  .tag5-2 {
    width: 180px;
    height: 12px;
  }

  @keyframes float {
    0% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-20px);
    }
    100% {
        transform: translateY(0px);
    }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-10px);
    }
    60% {
        transform: translateY(-5px);
    }
}

@keyframes shake {
  0% { transform: translate(0, 0); }
  20% { transform: translate(-2px, 2px); }
  40% { transform: translate(-2px, -2px); }
  60% { transform: translate(2px, 2px); }
  80% { transform: translate(2px, -2px); }
  100% { transform: translate(0, 0); }
}