.floating-button {
  position: fixed;
  right: 50px;
  bottom: 50px;
  width: 80px;
  height: 233px;
  background-color: #FFD100;
  border-radius: 41px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

/* 스크린 리더 전용 텍스트 스타일 */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.top-label-wrapper {
  position: relative;
  margin-top: 28px;
  cursor: pointer;
}

.top-label {
  font-size: 40px;
  font-weight: bold;
  color: #000;
  font-family: 'montserrat-black';
  transition: transform 0.2s ease-in-out;
}

.top-label-wrapper:hover {
  animation: shake 0.5s infinite alternate;
}

.tooltip {
  position: absolute;
  bottom: 15%; /* Make sure the tooltip appears above the top label */
  left: -220%;
  transform: translateX(-50%);
  margin-bottom: 5px;
  padding: 5px 10px;
  white-space: nowrap;
  background-color: white;
  color: black;
  font-size: 12px;
  font-family: 'pretendard-bold';
  border-radius: 5px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
  z-index: 1001; /* Ensure tooltip is on top */
}

.top-label-wrapper:hover .tooltip {
  opacity: 1;
  visibility: visible;
}

.bottom-box {
  width: 66px;
  height: 130px;
  background-color: #000;
  border-radius: 41px;
  margin-bottom: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icons {
  width: 33px;
  height: 33px;
  display: flex;
  flex-wrap: wrap;
  gap: 2px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

/* 호버 시 부르르 떠는 애니메이션 */
.icons:hover {
  animation: shake 0.5s infinite alternate;
}

.icon-circle {
  width: 13px;
  height: 13px;
  border: 1px solid #FFD100;
  border-radius: 50%;
  background-color: #000;
}

.icon-square {
  width: 13px;
  height: 13px;
  border: 1px solid #FFD100;
  background-color: #FFD100;
}

.pdf-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  cursor: pointer;
  text-decoration: none;
}

.consulting-link {
  width: 100%;
  height: 100%;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
}

.consulting-link:focus {
  outline: none;
}

.tooltip2 {
  position: absolute;
  bottom: 5%; /* Make sure the tooltip appears above the top label */
  left: -380%;
  margin-bottom: 5px;
  padding: 5px 10px;
  white-space: nowrap;
  background-color: white;
  color: black;
  font-size: 12px;
  font-family: 'pretendard-bold';
  border-radius: 5px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease-in-out, visibility  ease-in-out;
  z-index: 1001; /* Ensure tooltip is on top */
}

.icons:hover .tooltip2 {
  opacity: 1;
  visibility: visible;
}

@keyframes shake {
  0% { transform: translate(0, 0); }
  20% { transform: translate(-1px, 1px); }
  40% { transform: translate(-1px, -1px); }
  60% { transform: translate(1px, 1px); }
  80% { transform: translate(1px, -1px); }
  100% { transform: translate(0, 0); }
}